.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.t-left{
  text-align: left;
  font-size: 0.7em;
}

.fa-js-square{
  color: #f0db4f;
}

.fa-css3-alt{
  color: #2965f1;
}

.fa-html5{
  color: #e34c26;
}

.fa-react{
  color:  #61dbfb;
}

.fa-code{
  color: #fff;
}

.file-item{
  transition: all 0.5s ease-in-out;
  cursor: pointer;
  padding: 10px 20px;
  margin: 5px;
}

.file-item:hover{
  background-color: #363a42;
}
